.navbar
{
    width:100%;
    height:56px;
    position: fixed;
    top:0;
    left:0;
    display: flex;
    justify-content: center;
    background-color: #e7e7e7;
    align-items: center;
    padding: 5px 0;
    box-sizing: border-box;
    transition: all 0.3s ease;
}
.nav
{
    display: none;
    height:60px;
    padding:5px;
    box-sizing: border-box;
}
.nav > ul
{
    width: 100%;
    height: 100%;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3px 0; 
    box-sizing: border-box;
    transition: all 0.4s linear;
}
.logo
{
    width:50px;
    height:50px;
}
.brand
{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.brand *
{
    margin: 0 3px;
}
.title
{
    font-size: 1.8rem;
    font-weight: bolder;
}
.layover
{
    position: relative;
    width:100%;
    height: 100vh;
    top:0;
    left:0;
    background-color: #e7e7e7;
    z-index: 999;
    transition: all 0.5s ease;
}
.laynav
{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    width:50%;
}
.laynav > ul
{
    width: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 3px 0; 
    box-sizing: border-box;
}
.bars
{
    position:absolute;
    right: 10px;
    top: 12.5px;
    font-size: 1.5rem;
    display: block;
}
.exit
{
    position:absolute;
    right: 10px;
    top: 12.5px;
    font-size: 1.5rem;
    display: block;

}


.show{display: block;}.hide{display: none;}
@media only screen and (max-width: 575.98px) 
{ 
    .navbar{justify-content: flex-start;}
    .title{font-size:1.5 rem}
}
@media only screen and (min-width: 992px)
{
    .navbar
    {
        display:flex;
        justify-content: space-between;
        padding: 5px 5px 5px 20px;
    }
    .nav
    {
        display: block;
    }
    .bars{display: none;}
    /* .layover
    {
        display: none;
    } */
}